<template>
    <div class="home">
        <!-- 轮播图 -->
        <van-swipe :autoplay="5000" :show-indicators="false">
            <van-swipe-item v-for="(item, index) in bannerData" :key="index">
                <img class="auto-img" :src="item.bannerImg" alt />
                <div class="none">{{item.name}}</div>
            </van-swipe-item>
        </van-swipe>
        <div class="box">
            <!-- 欢迎信息 -->
            <div class="hello">
                <h1>
                    Hi,
                    <span v-show="isLogin">{{userInfo.nickName}}</span>
                    <span v-show="!isLogin" @click="login('Login')">请先登录</span>
                </h1>
                <h3>咖啡会让你脑洞大开哟</h3>
            </div>
            <!-- 购买方式 -->
            <div class="shopFuntion">
                <div class="takeOut" @click="goClassify">外送</div>
                <div class="SelfFaising" @click="goClassify">自提</div>
            </div>
            <!-- 热门推荐 -->
            <van-divider
                :style="{ color: '#353535', borderColor: '#989898', padding: '0 16px' }"
            >热门推荐</van-divider>
            <div class="hot">
                <van-grid :border="false" :column-num="2" gutter="8" round>
                    <van-grid-item
                        v-for="(item, index) in hotProducts"
                        :key="index"
                        @click="viewProductInfo(item.pid)"
                    >
                        <van-image :src="item.smallImg" />
                        <h3>{{item.name}}</h3>
                        <p>￥{{item.price}}</p>
                    </van-grid-item>
                </van-grid>
            </div>
        </div>
    </div>
</template>

<script>
import "../../assets/less/home.less";

export default {
    name: "Home",
    data() {
        return {
            bannerData: [],
            hotProducts: [],
            userInfo: {},
            isLogin: false,
        };
    },
    created() {
        this.getbannerData();
        this.getHotProducts();
        this.getUserInfo();
    },
    methods: {
        // 跳转至商品详情页
        viewProductInfo(pid) {
            this.$router.push({ name: "Detail", query: { pid } });
        },
        // 获取轮播图数据
        getbannerData() {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner",
            });
            this.axios({
                method: "GET",
                url: "/banner",
                params: {
                    appkey: this.appkey,
                },
            })
                .then((result) => {
                    this.$toast.clear();
                     
                    if (result.data.code == 300) {
                        this.bannerData = result.data.result;
                    }
                })
                .catch((err) => {
                    this.$toast.clear();
                     
                });
        },
        // 获取热门推荐数据
        getHotProducts() {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner",
            });
            this.axios({
                method: "GET",
                url: "/typeProducts",
                params: {
                    appkey: this.appkey,
                    key: "isHot",
                    value: 1,
                },
            })
                .then((result) => {
                    this.$toast.clear();
                     
                    if (result.data.code == 500) {
                        this.hotProducts = result.data.result;
                    }
                })
                .catch((err) => {
                    this.$toast.clear();
                     
                });
        },
        // 获取用户信息
        getUserInfo() {
            let tokenString = localStorage.getItem("CSTK");
            if (!tokenString) {
                this.isLogin = false;
                return;
            }

            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner",
            });

            this.axios({
                method: "GET",
                url: "/findMy",
                params: {
                    appkey: this.appkey,
                    tokenString,
                },
            })
                .then((result) => {
                    this.$toast.clear();
                    this.isLogin = true;
                    if (result.data.code == "A001") {
                        if (result.data.result.length > 0) {
                            this.isLogin = true;
                            this.userInfo = result.data.result[0];
                        }
                    }
                     
                })
                .catch((err) => {
                    this.$toast.clear();
                });
        },
        // 跳转至菜单页面
        goClassify() {
            this.$router.push({ name: "Classify" });
        },
    },
};
</script>

<style lang="less" scoped>
</style>